<template>
    <h1 :class="['basic-title', `is-${color}`]"><slot /></h1>
</template>

<script>
export default {
    name: 'index',
    props: {
        color: {
            type: String,
            default: 'dark',
            validator: (v) => {
                return v === 'light' || v === 'dark'
            }
        }
    }
}
</script>

<style scoped lang="scss">
    .basic-title {
        font-family: STSongti-SC, serif;
        font-size: 24px * 2;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        &.is-dark {
            color: #212121;
        }
        &.is-light {
            color: #ffffff;
        }
    }
</style>
